@font-face {
  font-family: AdidasFont;
  src: url("../AdihausDIN.073413d5.ttf");
}

* {
  box-sizing: border-box;
  letter-spacing: .01rem;
  margin: 0;
  padding: 0;
  font-family: AdidasFont, Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
}

body {
  background-color: #f3f3f3;
  width: 100%;
}

main {
  width: 100%;
}

header {
  border-bottom: 2px solid #000;
  width: 100%;
  height: auto;
  padding: 5rem 0;
  position: relative;
}

@media (width <= 48rem) {
  header {
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 0;
    display: flex;
  }
}

header .header-container {
  background-color: #0000;
  justify-content: space-between;
  align-items: center;
  width: min(90%, 90rem);
  margin-inline: auto;
  padding-right: 10rem;
  display: flex;
}

@media (width <= 48rem) {
  header .header-container {
    text-align: center;
    flex-direction: column;
    margin-bottom: 1.5rem;
    padding-right: 0;
  }
}

header .header-container .player-header-info {
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  display: flex;
}

@media (width <= 48rem) {
  header .header-container .player-header-info {
    align-items: center;
  }
}

header .header-container .player-header-info h2 {
  margin-bottom: .8rem;
  font-size: 2rem;
  font-weight: 700;
}

header .header-container .player-header-info .pos-number {
  margin-bottom: .1rem;
  font-size: 1.1rem;
}

header .header-container .player-header-info .pos-number strong {
  font-weight: 700;
}

header .header-container .player-header-info a {
  margin-bottom: 3rem;
  text-decoration: none;
}

@media (width <= 48rem) {
  header .header-container .player-header-info a {
    margin-bottom: .5rem;
  }
}

header .header-container .player-header-info .status span {
  color: #fff;
  border-radius: 1rem;
  padding: .5rem 1rem;
  display: inline-block;
}

@media (width <= 48rem) {
  header .header-container .player-header-info .status {
    margin-bottom: .5rem;
  }
}

header .header-container .player-photo {
  background-color: #0000;
  justify-content: center;
  align-items: center;
  width: 10rem;
  height: 11rem;
  display: flex;
}

@media (width <= 48rem) {
  header .header-container .player-photo {
    justify-content: center;
    align-items: center;
    margin-bottom: 1rem;
    display: flex;
  }
}

@media (width <= 26.25rem) {
  header .header-container .player-photo {
    width: 8rem;
  }
}

header .header-container .player-photo img {
  background-color: #f3f3f3;
  border: 1px solid #b9c1ba;
  height: 100%;
}

header .header-color {
  z-index: -3;
  background-color: #f3f3f3;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

header .secondary-color {
  z-index: -2;
  width: 50%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 50%;
}

@media (width <= 48rem) {
  header .secondary-color {
    width: 100%;
    height: 1rem;
    position: static;
    transform: none;
  }
}

header .primary-color {
  z-index: -1;
  width: 40%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 60%;
}

@media (width <= 48rem) {
  header .primary-color {
    width: 100%;
    height: 1rem;
    position: static;
    transform: none;
  }
}

.player-info {
  background-color: #f3f3f3;
  width: 100%;
  padding: 2rem 0;
}

.player-info-container {
  width: min(90%, 90rem);
  margin-inline: auto;
}

.cards-section {
  flex-direction: column;
  align-items: center;
  width: 100%;
  padding: 2rem 0;
  display: flex;
}

.cards-section h2 {
  text-transform: uppercase;
  text-align: center;
  width: 100%;
  margin-bottom: 2rem;
  font-size: 1.5rem;
}

@media (width <= 26.25rem) {
  .cards-section h2 {
    margin-bottom: 1rem;
  }
}

.cards-section .cards-container {
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem 1.5rem;
  width: 100%;
  display: grid;
}

@media (width <= 48rem) {
  .cards-section .cards-container {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (width <= 26.25rem) {
  .cards-section .cards-container {
    grid-template-columns: 1fr;
  }
}

.cards-section .cards-container .card h3 {
  text-transform: uppercase;
  margin-bottom: .5rem;
  font-size: 1.17rem;
}

.cards-section .cards-container .card p {
  font-size: 1rem;
}

.error-container {
  width: min(90%, 90rem);
  margin: 5rem auto;
}

.error-container h3 {
  margin-bottom: .5rem;
  font-size: 1.6rem;
  font-weight: 700;
}

@media (width <= 26.25rem) {
  .error-container h3 {
    font-size: 1.28rem;
  }
}

.error-container p {
  font-size: 1rem;
}

@media (width <= 26.25rem) {
  .error-container p {
    font-size: .9rem;
  }
}

